<template>
	<div class="z-add-img-container">
		<div class="mui-clearfix add-img-wrap">
			<!-- 图片组 -->
			<div class="mui-pull-left z-img-item" v-for='(item, ind) in list' :key='ind' :style="'background-image: url('+item.base64+')'">
				<i class="z-delect-btn" @tap='delect(ind)'></i>
			</div>

			<!-- 添加按钮 -->
			<div class="mui-pull-left z-add-btn" @tap='addEvent' v-if='list.length < opt.maxCount'>
				<i class="iconfont icon-Plus"></i>
			</div>
		</div>

		<input type="file" class='z-file-input-btn' accept="image/*" />
	</div>
</template>

<script>
import HeadCell from '@/components/Cell/HeadCell.vue'
import { selectImg, compressImage } from '@/common/js/utils'
export default {
	props: {
		options: {
			type: Object,
			default: function() {
				return {}
			}
		},
		list: {
			type: Array,
			default: function() {
				return []
			}
		}
	},
	computed: {
		opt() {
			let opt = {
				maxCount: 4,			// 最大数量
				compress: {}
			}
			return mui.extend(true, {}, opt, this.options)
		}
	},
	methods: {
		// 添加图片
		async addEvent() {
			try {
				let maximum = this.opt.maxCount - this.list.length
				let paths = await selectImg({multiple: true, maximum: maximum})
				if((paths.length + this.list.length) > this.opt.maxCount) {
					alert(`最多只能选${this.opt.maxCount}张图片`)
					return false;
				}
				for(let i=0, len=paths.length; i<len; i++) {
					let item = paths[i]
					let info = await compressImage(item, this.opt.compress)
					this.list.push(info)
				}
			} catch(e) {
				console.log(e);
			}
		},
		// 删除事件
		delect(index) {
			this.list.splice(index, 1)
		},
		// 获取图片
		getImages() {
			return this.list
		}
	}
}
</script>

<style lang='scss'>
$addBtnSize: calc(25vw - 17.5px);
$deleBtnSize: 18px;
$deleBtnPos: -5px;
.z-add-img-container {
	background-color: #fff;
	padding: 10px 15px;
	.z-img-item,
	.z-add-btn {
		width: $addBtnSize;
		height: $addBtnSize;
		line-height: $addBtnSize;
		margin: 5px;
		text-align: center;
	}
	/*图片组*/
	.z-img-item {
		position: relative;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		/*删除按钮*/
		.z-delect-btn {
			position: absolute;
			width: $deleBtnSize;
			height: $deleBtnSize;
			top: $deleBtnPos;
			right: $deleBtnPos;
			background-image: url(data:image/png;base64,);
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
		}
	}


	/*添加按钮*/
	.z-add-btn {
		border: 1px dashed #ccc;
		.iconfont {
			font-size: 4rem;
			color: #ccc;
		}
	}
}
</style>


